/*NAV:WIDE SCREEN*/

@media only screen and (min-width: 1001px) {
    /*main nav styling and positioning*/
    nav * {
        padding: 0;
        margin: 0;
    }
    nav {
        position: absolute;
        height: 5em;
        width: 100%;
        background-color: rgba($mainColor, 0.75);
        z-index: 10;
    }
    .nav-wrapper-outer {
        position: absolute;
        display: block;
        z-index: 2;
        width: 100%;
        height: 5em;
        line-height: 5em;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    }
    .nav-wrapper-inner {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
    }
    /*items styling and positioning*/
    .nav-wrapper-inner ul {
        height: 100%;
        list-style: none;
        z-index: 2;
    }
    .nav-wrapper-inner>ul {
        padding-right: 2.5em;
    }
    .nav-wrapper-inner ul li {
        height: 100%;
        position: relative;
        float: left;
        transition: background-color .3s linear;
    }
    .nav-wrapper-inner ul li:hover {
        background: $mainColorShade !important;
    }
    .nav-wrapper-inner ul li a:hover,
    .nav-wrapper-inner a.currentPage {
        color: $hoverColor !important;
    }
    /*DROPDOWN SUBNAV*/
    .navDropdown {
        width: 10em;
    }
    .subNav {
        width: 10em;
        background-color: $mainColor;
        left: 0;
        opacity: 0;
        position: absolute;
        /*Why did i do this?*/
        top: 0;
        visibility: hidden;
        z-index: 1;
        -webkit-transition: all .4s ease;
        transition: all .4s ease;
    }
    .subNav li {
        background-color: $mainColor;
        width: 10em;
    }
    .navDropdown:hover>.subNav {
        opacity: 1;
        top: 5em;
        visibility: visible;
        height: initial !important;
    }
    .subNav li a {
        width: 10em;
        height: initial;
    }
    /* Clearfix - self center its children suppoedly TEST THE NEED OF THIS
    .nav-wrapper-inner> ul:after,
    .nav-wrapper-inner> ul:before {
        content: "";
        display: table;
    }
    .nav-wrapper-inner> ul:after {
        clear: both;
    }
    /*TEXT*/
    .nav-wrapper-inner a {
        display: block;
        text-align: center;
        color: white;
        text-decoration: none;
        font-weight: bold;
        transition: background-color .3s;
        letter-spacing: .1em;
        padding: 0 .8em;
    }
    .subNav a {
        text-align: center;
        height: 4em !important;
        line-height: 4em !important;
        padding-top: 0 !important;
        width: initial !important;
    }
    /*IMAGE*/
    nav img {
        position: absolute;
        left: 2.5em;
        height: 4em;
        top: 50%;
        transform: translateY(-50%)
    }
    /*MOBILE NAV*/
    #nav-trigger {
        display: none;
    }
    .fixed-mobile-nav {
        display: none;
    }
    .clear-menu-btn {
        display: none;
    }
    .navHome {
        display: none;
    }
    .navDropdown input,
    .navDropdown>img {
        display: none;
    }
}


/*NAV:NARROW SCREEN*/

@media only screen and (max-width: 1000px) {
    nav * {
        padding: 0;
        margin: 0;
    }
    /* Navigation Menu - Background */
    .nav-wrapper-inner {
        background-color: $mainColor;
        /* critical sizing and position styles */
        width: 15.5em;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        /*for scrolling*/
        max-height: 100vh;
        display: flex;
        overflow-y: auto;
        overflow-x: hidden;
        flex-direction: column;
    }
    /* Navigation Menu - List items */
    .nav-wrapper-inner ul {
        width: 100%;
        line-height: 4em;
    }
    .nav-wrapper-inner ul li {
        /* non-critical appearance styles */
        width: 100%;
        transition: background-color .3s linear;
        display: block;
        overflow: hidden;
    }
    .nav-wrapper-inner ul li:hover {
        background-color: $mainColorShade;
    }
    .nav-wrapper-inner .subNav a {
        padding-left: 1em;
    }
    .nav-wrapper-inner a {
        display: block;
        color: white;
        text-decoration: none;
        font-weight: bold;
        transition: .3s linear;
        text-align: left;
        font-size: 1.5em;
        padding-left: .5em;
    }
    .nav-wrapper-inner a:hover,
    .nav-wrapper-inner a.currentPage {
        color: $hoverColor;
    }
    /* Site Wrapper - Everything that isn't navigation */
    #wrapper {
        /* Critical position and size styles */
        min-height: 100%;
        min-width: 100%;
        background-color: white;
        /* Needs a background or else the nav will show through */
        position: relative;
        top: 0;
        bottom: 100%;
        right: 0;
        z-index: 1;
        box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.5);
    }
    /*following header*/
    .fixed-mobile-nav {
        width: 100%;
        height: 4em;
        background-color: $mainColor;
        box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.5);
        position: fixed;
        z-index: 10;
        transition: .5s linear;
    }
    .nav-trigger:checked~.fixed-mobile-nav {
        box-shadow: 0 0 7px 7px rgba(0, 0, 0, 0.4);
    }
    /*space for mobile nav*/
    .navHome {
        margin-top: 6.2em;
        width: 100%;
    }
    .nav-wrapper-outer img {
        position: fixed;
        left: .5em;
        top: .25em;
        z-index: 12;
        height: 3.5em;
    }
    .mobile-nav-spacer {
        height: 4em;
        width: 100%;
    }
    /* Pure CSS3 Hamburger*/
    .clear-menu-btn {
        position: fixed;
        right: .75em;
        top: .75em;
        z-index: 12;
        width: 2.5em;
        height: 2.5em;
        margin: 0 auto;
    }
    .nav-trigger:hover {
        cursor: pointer;
    }
    .nav-trigger:checked~.clear-menu-btn .top,
    .nav-trigger:checked~.clear-menu-btn .bottom {
        top: 50%;
    }
    .nav-trigger:checked~.clear-menu-btn .top {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
    }
    .nav-trigger:checked~.clear-menu-btn .bottom {
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
    }
    .nav-trigger:checked~.clear-menu-btn .middle {
        filter: alpha(opacity=0);
        opacity: 0;
    }
    .nav-trigger:checked~.clear-menu-btn .circle {
        filter: alpha(opacity=100);
        opacity: 1;
    }
    .clear-menu-btn span {
        position: absolute;
        display: block;
        width: 100%;
        height: .12em;
        background-color: $navHam;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        z-index: -1;
    }
    .clear-menu-btn .top {
        top: 15%;
        z-index: 1;
    }
    .clear-menu-btn .middle {
        top: 50%;
        z-index: 1;
    }
    .clear-menu-btn .bottom {
        top: 85%;
        z-index: 1;
    }
    .clear-menu-btn .circle {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        filter: alpha(opacity=0);
        opacity: 0;
        width: 100%;
        height: 100%;
        background-color: transparent;
        border-radius: 50%;
        border: .14em solid $navHam;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -ms-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        z-index: -1;
    }
    /* Nav Trigger */
    .nav-trigger {
        /* critical styles - hide the checkbox input*/
        position: fixed;
        z-index: 13;
        top: .5em;
        right: .5em;
        height: 3em;
        width: 3em;
        filter: alpha(opacity=0);
        opacity: 0;
    }
    /* Make the Magic Happen */
    #wrapper {
        transition: right .5s;
    }
    .nav-trigger:checked~#wrapper {
        /*The window*/
        right: 15.5em;
    }
    /* Without this, the body has excess horizontal scroll when the menu is open */
    body {
        overflow-x: hidden;
    }
    .navDropdown {
        position: relative;
    }
    .navDropdown input,
    .navDropdown>img {
        position: absolute;
        right: 1em;
        left: initial;
        cursor: pointer;
    }
    .navDropdown input {
        filter: alpha(opacity=0);
        opacity: 0;
        top: 1em;
        width: 3em;
        height: 3em;
        z-index: 15;
    }
    .navDropdown input:checked~img {
        transform: rotate(180deg);
    }
    .navDropdown>img {
        transition: .5s linear;
        top: 1em;
        width: 2em;
        height: 2em;
    }
    .navDropdown .subNav {
        max-height: 0;
        -webkit-transition: all 0.5s ease-in-out;
        -moz-transition: all 0.5s ease-in-out;
        -o-transition: all 0.5s ease-in-out;
        -ms-transition: all 0.5s ease-in-out;
        transition: all 0.5s ease-in-out;
    }
    .navDropdown input:checked~.subNav {
        max-height: 15em;
    }
}
